/**
 * AI OSS 上传器
 * 封装了从 STS 获取凭证、文件 SM4 加密以及阿里云 OSS 分片上传的完整流程。
 * 支持：分片上传、断点续传、进度回调、上传暂停。
 */

import OSS from "ali-oss";
import { STSProvider } from "./sts-provider.js";
import { sm4 } from 'sec-crypto'
import { v4 as uuidv4 } from 'uuid';

/**
 * 读取 File 为 ArrayBuffer
 */
async function readFileAsArrayBuffer(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = () => resolve(reader.result);
    reader.onerror = reject;
    reader.readAsArrayBuffer(file);
  });
}

/**
 * Base64 转 HEX
 */
function base64toHEX(base64) {
  const raw = window.atob(base64);
  let HEX = "";
  for (let i = 0; i < raw.length; i++) {
    const _hex = raw.charCodeAt(i).toString(16);
    HEX += _hex.length === 2 ? _hex : "0" + _hex;
  }
  return HEX;
}

/**
 * DataURL (Base64) 转 Blob
 */
function dataURLtoBlob(dataurl) {
  if (!dataurl || typeof dataurl !== 'string') {
    throw new Error('dataURLtoBlob: dataurl must be a non-empty string');
  }
  
  const arr = dataurl.split(",");
  if (arr.length < 2) {
    throw new Error('dataURLtoBlob: invalid data URL format, missing comma separator');
  }
  
  const mimeMatch = arr[0].match(/:(.*?);/);
  if (!mimeMatch || !mimeMatch[1]) {
    throw new Error('dataURLtoBlob: invalid data URL format, missing MIME type');
  }
  
  const mime = mimeMatch[1];
  const bstr = window.atob(arr[1]);
  let n = bstr.length;
  const u8arr = new Uint8Array(n);
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n);
  }
  return new Blob([u8arr], { type: mime });
}

/**
 * 使用 SM4 对二进制数据加密
 */
function sm4Encrypt(arrayBuffer, kmsDataKey) {
  const bytes = new Uint8Array(arrayBuffer);
  const hexData = Array.from(bytes)
    .map((b) => b.toString(16).padStart(2, "0"))
    .join("");

  const hexDataKey = base64toHEX(kmsDataKey.dataKey);

  const encryptedHex = sm4.encrypt(hexData, hexDataKey.slice(0, 32), {
    iv: hexDataKey.slice(32, 64),
    mode: "cbc",
  });

  const result = new Uint8Array(
    encryptedHex.match(/[\da-f]{2}/gi).map((h) => parseInt(h, 16))
  );

  return result;
}

/**
 * 将 File 加密后构造新的 Blob
 */
async function createEncryptedBlob(file, { kmsDataKey }) {
  const arrayBuffer = await readFileAsArrayBuffer(file);
  const encryptedData = sm4Encrypt(arrayBuffer, kmsDataKey);
  return new Blob([encryptedData], { type: file.type });
}

export class OssUploader {
  constructor() {
    this.stsProvider = new STSProvider();
  }

  /**
   * 全局配置
   */
  static config(options) {
    STSProvider.config(options);
  }

  /**
   * 上传单个文件 (使用分片上传模式)
   * @param {File|Blob|string} file - 要上传的文件 (File, Blob, 或 Base64 字符串)
   * @param {string} bizCode - 业务编码
   * @param {Object} options - 配置项
   * @param {string} options.fileName - 自定义文件名
   * @param {string} options.strictNameMode - 严格名称
   * @param {Object} options.checkpoint - 断点信息（用于续传）
   * @param {Function} options.onProgress - 进度回调 (percent, checkpoint) => {}
   * @param {Function} options.onCancelTask - 获取取消句柄的回调 (cancelHandle) => {}
   * @returns {Promise<Object>} OSS 上传结果
   */
  async upload(file, bizCode, options = {}) {
    if (!file || !bizCode) {
      throw new Error("OssUploader: file and bizCode are required");
    }

    try {
      // 0. 归一化输入：如果是 base64 字符串，转换为 Blob
      let normalizedFile = file;
      if (typeof file === "string" && file.startsWith("data:")) {
        normalizedFile = dataURLtoBlob(file);
      }

      // --- 新增：HTML 文件校验逻辑 ---
      const checkName = normalizedFile.name || "";
      const checkType = normalizedFile.type || "";
      const isHtml = checkName.toLowerCase().endsWith('.html') || 
                     checkName.toLowerCase().endsWith('.htm') || 
                     checkType === 'text/html';

      if (isHtml && !options.allowHTML) {
        const errorMsg = "暂不支持上传 HTML 格式文件";
        if (typeof window !== 'undefined' && window.alert) {
          window.alert(errorMsg);
        }
        throw new Error(`OssUploader: ${errorMsg}`);
      }

      // 1. 获取 STS 凭证
      const stsData = await this.stsProvider.getCredentials({ bizCode });

      // 2. 处理文件加密 (注意：分片上传建议先完成整体加密)
      let uploadFile = normalizedFile;
      let meta = {};

      if (stsData.encryptEnable) {
        if (stsData.kmsDataKey && stsData.kmsDataKey.sm4Supported) {
          // 1. 我们内部的 SM4 加密
          uploadFile = await createEncryptedBlob(normalizedFile, stsData)
          meta = {
            'encrypted-version': String(stsData.kmsDataKey.version),
            'encrypted-data-key': String(stsData.kmsDataKey.dataKeyEncrypted),
            'sm4-supported': '1'
          }
        } else if (typeof options.externalEncrypt === 'function') {
          // 2. 由外部注入加密逻辑（兼容旧加密方案）
          const { encryptedFile, meta: extraMeta } = await options.externalEncrypt(normalizedFile, stsData)
          uploadFile = encryptedFile
          meta = extraMeta || {}
        } else {
          // 3. 否则：仅告警，不做加密，交给调用方自己决定要不要用这个类
          console.warn('OssUploader: encryptEnable=true 但不支持 SM4，且未提供 externalEncrypt 回调，按未加密处理')
        }
      }

      // 3. 准备 OSS 客户端
      const client = new OSS({
        accessKeyId: stsData.accessKeyId,
        accessKeySecret: stsData.accessKeySecret,
        stsToken: stsData.securityToken,
        bucket: stsData.bucket,
        endpoint: stsData.endpoint,
        cname: !!stsData.cname,
        region: stsData.region || undefined,
        secure: true,
      });

      // 4. 路径处理
      // 注意：Blob 没有 name 属性，需要处理
      const originalName = normalizedFile.name || (normalizedFile.type ? `upload.${normalizedFile.type.split('/')[1]}` : "unnamed");
      const fileName = (options.strictNameMode && options.fileName) || this.generateUniqueFileName(originalName, options.fileName);
      const objectKey = `${stsData.object}/${fileName}`.replace(/\/+/g, "/");
      // 关键信息：保存最新的 checkpoint（里面有 uploadId）
      let latestCheckpoint = options.checkpoint || null;
      let isUploadActive = true;

      // 先把 abort 函数暴露给外面（里面用的是闭包里的 latestCheckpoint）
      if (options.onAbortHandler) {
        options.onAbortHandler(async () => {
          // 已经结束了，就不掉方法了
          if (!isUploadActive) return;
          isUploadActive = false;
          if (client) {
            // 先尝试取消当前请求
            if (typeof client.cancel === 'function') {
              client.cancel();
            }
            if (latestCheckpoint && latestCheckpoint.uploadId) {
              await client.abortMultipartUpload(
                objectKey,
                latestCheckpoint.uploadId
              );
            }
          }
        });
      }

      // 提供暂停（仅取消请求，保留分片）句柄
      if (options.onPauseHandler) {
        options.onPauseHandler(() => {
          if (!isUploadActive) return;
          if (client && typeof client.cancel === 'function') {
            client.cancel();
          }
        });
      }

      // 5. 执行分片上传
      const result = await client.multipartUpload(objectKey, uploadFile, {
        checkpoint: options.checkpoint,
        meta,
        progress: (p, cpt) => {
          console.log(p, cpt, "progress");
          // cpt 里包含 uploadId 和已上传的分片信息
          latestCheckpoint = cpt || latestCheckpoint; // 更新最新的 checkpoint
          if (options.onProgress) {
            // 返回进度百分比和当前的 checkpoint
            options.onProgress(Math.floor(p * 100), cpt);
          }
        },
      });

      isUploadActive = false

      // 优先使用后端返回的 domain + objectKey 组合 URL（而不是 endpoint）
      let finalUrl = "";
      let originUrl = "";
      if (stsData.domain) {
        const domain = stsData.domain.replace(/\/+$/, "");
        const key = objectKey.replace(/^\/+/, "");
        finalUrl = `${domain}/${key}`;
        originUrl = `${domain}/${key}`;
      } else if (result.url) {
        finalUrl = result.url;
        originUrl = result.url;
      } else if (
        result.res &&
        result.res.requestUrls &&
        result.res.requestUrls[0]
      ) {
        finalUrl = result.res.requestUrls[0];
        originUrl = result.res.requestUrls[0];
      }

      // 如果开启了加密，追加访问参数
      if (stsData.encryptEnable && stsData.kmsDataKey) {
        const separator = finalUrl.includes("?") ? "&" : "?";
        const accessKey = stsData.kmsDataKey.accessKey || "";
        finalUrl = `${finalUrl}${separator}accessKey=${accessKey}&v=1&encrypted=1`;
      }

      return {
        ...result,
        fileName,
        url: finalUrl,
        originUrl: originUrl,
        stsData
      };
    } catch (error) {
      if (typeof isUploadActive !== 'undefined') {
        isUploadActive = false
      }
      // 判断是否是主动取消
      if (error.name === "cancel") {
        console.log("OssUploader: Upload canceled by user");
      }
      console.error("OssUploader: Upload failed", error);
      throw error;
    }
  }

  /**
   * 上传多个文件
   * @param {Array<File>} files
   * @param {string} bizCode
   * @param {Object} options
   */
  async uploadMultiple(files, bizCode, options = {}) {
    const promises = files.map((file, index) => {
      // 注意：多文件上传时，如果需要分别控制暂停，options 里的回调需要处理 index
      return this.upload(file, bizCode, {
        ...options,
        onProgress: (p, cpt) => {
          if (options.onItemProgress) {
            options.onItemProgress(index, p, cpt);
          }
        },
        onCancelTask: (cancel) => {
          if (options.onItemCancelTask) {
            options.onItemCancelTask(index, cancel);
          }
        },
         // 新增：传递暂停句柄
        onPauseHandler: (pauseFn) => {
          if (options.onItemPauseTask) {
            options.onItemPauseTask(index, pauseFn);
          }
        },
        // 新增：传递中止句柄
        onAbortHandler: (abortFn) => {
          if (options.onItemAbortTask) {
            options.onItemAbortTask(index, abortFn);
          }
        },
      });
    });

    return Promise.allSettled(promises);
  }

  /**
   * 生成唯一文件名
   */
  generateUniqueFileName(originalName, fileName) {
    const randomStr = uuidv4()
    let ext = originalName.split(".").pop();
    if(ext === 'unnamed' && fileName) {
      ext = fileName.split(".").pop();
    }
    return `${randomStr}.${ext}`;
  }

  /**
   * 清除所有 STS 缓存
   */
  static clearCache() {
    STSProvider.clearCache();
  }
}
